import { Component } from 'react';
import { Editor, EditorState, RichUtils, ContentState } from 'draft-js';
import { connect } from 'react-redux';
import htmlToDraft from 'html-to-draftjs';
import NoSSR from 'react-no-ssr';
class EditArticle1 extends Component {
  title='';
  constructor(props) {
    super(props)
    const { selectedArticle}= this.props;
    this.title = selectedArticle.title;
    const contentBlock = htmlToDraft(this.title);
    if(contentBlock) {
      const contentState = ContentState.createFromBlockArray(contentBlock.contentBlocks);
      const editorState = EditorState.createWithContent(contentState);
      this.state = {
        editorState
      }
    }
   
  }
  onEditorStateChange = (editorState) => {
    this.setState({
      editorState
    });
  };

  onChange = (editorState) => {
    this.setState({
      editorState 
    }) 
  }

  handleKeyCommand = (command) => {
    const newState = RichUtils.handleKeyCommand(this.state.editorState, command);
    if (newState) {
      this.onChange(newState);
      return 'handled';
    }
    return 'not-handled';
  }

  onUnderlineClick = () => {
    this.onChange(RichUtils.toggleInlineStyle(this.state.editorState, 'UNDERLINE'));
  }

  onBoldClick = () => {
    this.onChange(RichUtils.toggleInlineStyle(this.state.editorState, 'BOLD'))
  }

  onItalicClick = () => {
    this.onChange(RichUtils.toggleInlineStyle(this.state.editorState, 'ITALIC'))
  }


  render() {
    const { selectedArticle}= this.props;
    const { editorState } = this.state;

    return(
      <div className="editorContainer">
        <button onClick={this.onUnderlineClick}>U</button>
        <button onClick={this.onBoldClick}><b>B</b></button>
        <button onClick={this.onItalicClick}><em>I</em></button>        
        <div className="editors">
        <div>{selectedArticle.title}</div>
        <Editor 
          editorState={editorState}
          wrapperClassName="title__wrapper"
          editorClassName="demo-editor"
          onEditorStateChange={this.onEditorStateChange}
          />
          </div>
      </div>
    )
  }
}

const mapStateToProps = ({ selectedArticle }) => ({ selectedArticle })

export default connect(mapStateToProps)(EditArticle1);